import React, { useRef, useEffect, useState } from "react";
import { useFrame } from "@react-three/fiber";
import * as THREE from "three";

const LoadPortalScene: React.FC<{ portalId: number; maxScroll: number; children?: React.ReactNode }> = ({ portalId, maxScroll, children }) => {
  console.log(`Loading portal scene ${portalId}`);

  const groupRef = useRef<THREE.Group>(null);
  const [scrollOffset, setScrollOffset] = useState(0);

  useEffect(() => {
    const handleWheel = (event: WheelEvent) => {
      // Update the scroll offset based on the wheel delta
      setScrollOffset((prevOffset) => THREE.MathUtils.clamp(prevOffset + event.deltaY * 0.003, 0, maxScroll));
    };

    // Attach the wheel event listener
    window.addEventListener("wheel", handleWheel);

    return () => {
      // Clean up the event listener
      window.removeEventListener("wheel", handleWheel);
    };
  }, [maxScroll]);

  useFrame(() => {
    if (groupRef.current) {
      // Update the group's position based on the scroll offset
      groupRef.current.position.y = +scrollOffset; // Negative for upward movement
    }
  });

  return <group ref={groupRef}>{children}</group>;
};

export default LoadPortalScene;
