import React from "react";
import { useThree } from "@react-three/fiber";
import * as THREE from "three";

const AddCameraParent: React.FC = () => {
  const { scene } = useThree();

  const cameraParent = new THREE.Group();
  cameraParent.position.set(0, 0, 0);
  cameraParent.name = "Camera Parent";

  // add a group to the scene that will be the parent of the camera
  scene.add(cameraParent);

  return null;
};

export default AddCameraParent;
