import React, { useRef, useEffect } from "react";
import { useFrame, useThree } from "@react-three/fiber";
import * as THREE from "three";

const CameraController: React.FC = () => {
  const { camera } = useThree();
  const targetOffset = useRef(new THREE.Vector3()); // Target offset for the camera
  const originalPosition = useRef(camera.position.clone()); // Store the original camera position
  const dampingFactor = 0.01; // Damping factor for smooth movement
  const maxOffset = 1.5; // Max offset for camera movement
  const returnSpeed = 0.02; // Speed at which the camera returns to the original position
  const lastGamma = useRef(0);
  const lastBeta = useRef(0);

  useEffect(() => {
    const handleMouseMove = (event: MouseEvent) => {
      const movementX = event.movementX * 0.01 || 0;
      const movementY = event.movementY * 0.01 || 0;

      targetOffset.current.x = THREE.MathUtils.clamp(targetOffset.current.x + movementX, -maxOffset, maxOffset);
      targetOffset.current.y = THREE.MathUtils.clamp(targetOffset.current.y - movementY, -maxOffset, maxOffset);
    };

    const handleDeviceOrientation = (event: DeviceOrientationEvent) => {
      if (event.gamma === null || event.beta === null) return;

      // Find out how much the device has moved since the last event
      const gammaDiff = event.gamma - lastGamma.current;
      const betaDiff = event.beta - lastBeta.current;

      targetOffset.current.x = THREE.MathUtils.clamp(targetOffset.current.x + gammaDiff * 0.06, -maxOffset, maxOffset);
      targetOffset.current.y = THREE.MathUtils.clamp(targetOffset.current.y - betaDiff * 0.06, -maxOffset, maxOffset);

      lastGamma.current = event.gamma;
      lastBeta.current = event.beta;
    };

    // Attach mouse move and device orientation listeners
    window.addEventListener("mousemove", handleMouseMove);
    window.addEventListener("deviceorientation", handleDeviceOrientation);

    return () => {
      window.removeEventListener("mousemove", handleMouseMove);
      window.removeEventListener("deviceorientation", handleDeviceOrientation);
    };
  }, []);

  useFrame(() => {
    // Smoothly interpolate the camera position towards the target offset
    camera.position.lerp(originalPosition.current.clone().add(targetOffset.current), dampingFactor);

    // Gradually move the camera back to the original position when idle
    targetOffset.current.lerp(new THREE.Vector3(0, 0, 0), returnSpeed);

    // Ensure the camera continues looking at the same point
    camera.lookAt(0, 0, 0);
  });

  return null; // This component doesn't render anything directly
};

export default CameraController;
