import React, { useEffect } from "react";
import { useThree } from "@react-three/fiber";
import * as THREE from "three";

const DynamicCameraFov: React.FC<{ frameSize: number; fixedDistance?: number }> = ({ frameSize, fixedDistance = 5 }) => {
  const { camera, size } = useThree();

  useEffect(() => {
    // Make sure the camera is a perspective camera
    if (camera.type === "PerspectiveCamera") {
      const adjustCameraFov = () => {
        // Use the smaller of the width or height for consistent sizing
        const smallestDimension = Math.min(size.width, size.height);

        // Calculate the fov based on the smallest dimension
        const distance = fixedDistance;
        const fov = 2 * Math.atan(frameSize / (2 * distance)) * (180 / Math.PI); // In degrees

        // Adjust based on the smallest dimension
        const adjustedFov = fov * (size.height / smallestDimension);

        const perspectiveCamera = camera as THREE.PerspectiveCamera;
        perspectiveCamera.fov = adjustedFov;
        perspectiveCamera.updateProjectionMatrix(); // Recalculate the projection matrix
        console.log(`Adjusted camera fov to ${adjustedFov}`);
      };

      // Adjust the camera once on load and on window resize
      adjustCameraFov();
      window.addEventListener("resize", adjustCameraFov);

      return () => {
        window.removeEventListener("resize", adjustCameraFov);
      };
    }
  }, [camera, size.width, size.height]);

  return null;
};

export default DynamicCameraFov;
