import React, { useState } from "react";
import * as THREE from "three";
import { Canvas } from "@react-three/fiber";
import { Stats } from "@react-three/drei";

// Custom imports
import "./modules/NpgPortalCube";
import NpgPortalCube from "./modules/NpgPortalCube";
import CameraMovement from "@/components/3d/CameraMovement";
import AddCameraParent from "@/utils/camera/AddCameraParent";
import DynamicCamera from "@/utils/camera/DynamicCameraFov";
import HamburgerMenu from "@/components/2d/HamburgerMenu";

import { npgPortalCubeData } from "./data/data";

const App: React.FC = () => {
	const [showStats, setShowStats] = useState(false);
	const handleToggleStats = () => {
		setShowStats((prev) => !prev);
	};

	return (
		<div style={{ width: "100vw", height: "100vh", overflow: "hidden" }}>
			<Canvas
				camera={{
					position: new THREE.Vector3(0, 0, 6),
					fov: 50,
					near: 0.2,
					far: 20,
				}}
				gl={{
					powerPreference: "high-performance",
					alpha: false,
					antialias: true,
					shadowMapEnabled: false,
				}}
				shadows={false}
			>
				<color attach="background" args={["#ffffff"]} />
				<DynamicCamera frameSize={3} />
				<AddCameraParent />
				<NpgPortalCube {...npgPortalCubeData} />
				<CameraMovement />
				{showStats && <Stats />}
			</Canvas>
			{/* UI Overlay */}
			<div id="ui-overlay" style={{ position: "absolute", top: 0, left: 0, width: "100%", height: "100%", pointerEvents: "none" }}>
				<HamburgerMenu showStats={showStats} onToggleStats={handleToggleStats} />
			</div>
		</div>
	);
};

export default App;
