import React, { useMemo, useRef, useEffect, useState } from "react";
import * as THREE from "three";
import { useFrame, useThree } from "@react-three/fiber";
import { useCursor, useGLTF } from "@react-three/drei";
import { MeshPortalMaterial, Text } from "@react-three/drei";
import { animated, useSpring } from "@react-spring/three";
import LoadPortalScene from "./LoadPortalScene";
import BackButton from "@2dc/BackButton";
export interface PortalCubeProps {
	cubeModelPath: string;
	portalData: PortalData[];
	rotationSpeed?: number;
}

export interface PortalData {
	meshId: string;
	texturePath: string;
	normal: THREE.Vector3;
	header: string;
	tagline?: string;
}

export interface Portal {
	id: number;
	node: THREE.Mesh;
	texture: THREE.Texture;
	normal: THREE.Vector3;
	header: string;
	tagline?: string;
}

const NpgPortalCube: React.FC<PortalCubeProps> = ({ cubeModelPath, portalData, rotationSpeed = 0.01 }) => {
	const { camera, scene } = useThree();

	// Load the GLTF model and extract nodes
	const { nodes } = useGLTF(cubeModelPath);

	const [currentPortal, setCurrentPortal] = useState<Portal | null>(null);
	const [isInPortal, setIsInPortal] = useState(false);

	// State to keep track of visible portals
	const [visiblePortals, setVisiblePortals] = useState<number[]>([]);
	const isAnimating = useRef(false);

	const animatingTransition = useRef(false);

	// References for the group and quaternion
	const groupRef = useRef<THREE.Group>(null);
	const quaternion = useRef(new THREE.Quaternion());

	// Flags and variables for dragging functionality
	const isDragging = useRef(false);
	const lastMousePosition = useRef({ x: 0, y: 0 });

	// Camera position
	const cameraLocation = useThree((state) => state.camera.position.clone());

	// Axes and temporary quaternions for rotation calculations
	const xAxis = useRef(new THREE.Vector3(1, 0, 0));
	const yAxis = useRef(new THREE.Vector3(0, 1, 0));
	const tempQuatX = useRef(new THREE.Quaternion());
	const tempQuatY = useRef(new THREE.Quaternion());

	const portals = useMemo(() => {
		const textureLoader = new THREE.TextureLoader();
		return portalData.map((data, index) => ({
			id: index,
			node: nodes[data.meshId] as THREE.Mesh,
			texture: textureLoader.load(data.texturePath, (texture) => (texture.flipY = false)),
			normal: data.normal,
			header: data.header,
			tagline: data.tagline,
		}));
	}, []);

	// Spring animation for rotating the cube to the closest face
	const [rotation, api] = useSpring(() => ({
		springRotation: groupRef.current ? [groupRef.current.rotation.x, groupRef.current.rotation.y, groupRef.current.rotation.z] : [0, 0, 0],
		config: { mass: 2, tension: 300, friction: 40, precision: 0.01 },
		onStart: () => {
			isAnimating.current = true;
		},
		onRest: () => {
			isAnimating.current = false;
		},
	}));

	const calculateVisiblePortals = () => {
		if (!groupRef.current) return;

		const worldMatrix = groupRef.current.matrixWorld;
		const updatedVisiblePortals: number[] = [];

		portals.forEach(({ id, normal }) => {
			// Transform the normal to world space
			const worldNormal = normal.clone().applyMatrix4(worldMatrix).normalize();
			const toCamera = cameraLocation.clone().sub(groupRef.current!.position).normalize();

			// Check visibility using the dot product
			if (worldNormal.dot(toCamera) > 0) {
				updatedVisiblePortals.push(id);
			}
		});

		setVisiblePortals(updatedVisiblePortals);
	};

	// ** Pointer events for dragging the cube **

	// Start dragging the cube when pointer is down
	const handlePointerDown = (e: PointerEvent) => {
		if (animatingTransition.current) return;
		isDragging.current = true;
		lastMousePosition.current = { x: e.clientX, y: e.clientY };
	};

	// Rotate the cube when pointer is moved and dragging
	const handlePointerMove = (e: PointerEvent) => {
		if (!isDragging.current || !groupRef.current) return;
		if (animatingTransition.current) return;

		const deltaX = e.clientX - lastMousePosition.current.x;
		const deltaY = e.clientY - lastMousePosition.current.y;

		// Update temporary quaternions for rotation
		tempQuatX.current.setFromAxisAngle(xAxis.current, deltaY * rotationSpeed);
		tempQuatY.current.setFromAxisAngle(yAxis.current, deltaX * rotationSpeed);

		// Combine rotations and update quaternion
		quaternion.current.multiplyQuaternions(tempQuatY.current, quaternion.current); // Apply Y first
		quaternion.current.multiplyQuaternions(tempQuatX.current, quaternion.current); // Apply X next

		// Apply the quaternion to the group
		groupRef.current.quaternion.copy(quaternion.current);
		const euler = new THREE.Euler().setFromQuaternion(quaternion.current);

		// Spring animation
		api.start({
			from: { springRotation: [groupRef.current.rotation.x, groupRef.current.rotation.y, groupRef.current.rotation.z] },
			to: { springRotation: [euler.x, euler.y, euler.z] },
		});

		// Update the last mouse position
		lastMousePosition.current = { x: e.clientX, y: e.clientY };
	};

	// Stop dragging the cube when pointer is up, and align the cube to the closest face
	const handlePointerUp = () => {
		isDragging.current = false;

		// Align cube to the closest face
		alignCubeToClosestFace();
	};

	// Align the cube so the face that is closest to the camera is facing the camera head-on
	const alignCubeToClosestFace = () => {
		if (!groupRef.current) return;
		// calculate what face is the closest to facing the camera
		const closestFace = portals.reduce(
			(closest, portal) => {
				const normal = portal.normal.clone().applyQuaternion(groupRef.current!.quaternion);
				const dot = normal.dot(cameraLocation.clone().sub(groupRef.current!.position).normalize());
				return dot > closest.dot ? { id: portal.id, dot } : closest;
			},
			{ id: -1, dot: -Infinity }
		);

		function normalizeRotation(targetValue: number, currentValue: number) {
			// Check if the target value is more or less that 180 degrees from the current value
			const diff = targetValue - currentValue;
			if (diff > Math.PI + Math.PI / 2) return targetValue - Math.PI * 2;
			if (diff > Math.PI / 2) return targetValue - Math.PI;
			if (diff < -Math.PI - Math.PI / 2) return targetValue + Math.PI * 2;
			if (diff < -Math.PI / 2) return targetValue + Math.PI;
			return targetValue;
		}

		const isCubeUpright = (targetRotation: THREE.Euler, upVector: THREE.Vector3) => {
			const targetQuaternion = new THREE.Quaternion().setFromEuler(targetRotation);
			upVector.applyQuaternion(targetQuaternion);
			const dot = upVector.dot(new THREE.Vector3(0, 1, 0));
			return dot > 0;
		};

		let targetRotation = new THREE.Euler();

		// Align the cube to the closest face
		switch (closestFace.id) {
			case 0: // Back -- Design
				quaternion.current.setFromAxisAngle(new THREE.Vector3(0, 1, 0), Math.PI);
				targetRotation = new THREE.Euler(0, Math.PI, 0);
				targetRotation.x = normalizeRotation(targetRotation.x, groupRef.current.rotation.x);
				targetRotation.y = normalizeRotation(targetRotation.y, groupRef.current.rotation.y);
				targetRotation.z = normalizeRotation(targetRotation.z, groupRef.current.rotation.z);
				// Flip the cube if it's upside down
				if (!isCubeUpright(targetRotation, new THREE.Vector3(0, 1, 0))) {
					targetRotation.z += Math.PI;
				}
				break;
			case 1: // Bottom -- Event
				quaternion.current.setFromAxisAngle(new THREE.Vector3(1, 0, 0), -Math.PI / 2);
				targetRotation = new THREE.Euler(-Math.PI / 2, 0, 0);
				targetRotation.x = normalizeRotation(targetRotation.x, groupRef.current.rotation.x);
				targetRotation.y = normalizeRotation(targetRotation.y, groupRef.current.rotation.y);
				targetRotation.z = normalizeRotation(targetRotation.z, groupRef.current.rotation.z);
				// Flip the cube if it's upside down
				if (!isCubeUpright(targetRotation, new THREE.Vector3(0, 0, 1))) {
					targetRotation.y += Math.PI;
				}
				break;
			case 2: // Top -- Exibition
				quaternion.current.setFromAxisAngle(new THREE.Vector3(1, 0, 0), Math.PI / 2);
				targetRotation = new THREE.Euler(Math.PI / 2, 0, 0);
				targetRotation.x = normalizeRotation(targetRotation.x, groupRef.current.rotation.x);
				targetRotation.y = normalizeRotation(targetRotation.y, groupRef.current.rotation.y);
				targetRotation.z = normalizeRotation(targetRotation.z, groupRef.current.rotation.z);
				// Flip the cube if it's upside down
				if (!isCubeUpright(targetRotation, new THREE.Vector3(0, 0, -1))) {
					targetRotation.y += Math.PI;
				}
				break;
			case 3: // Right -- Interactive
				quaternion.current.setFromAxisAngle(new THREE.Vector3(0, 1, 0), -Math.PI / 2);
				targetRotation = new THREE.Euler(0, -Math.PI / 2, 0);
				targetRotation.x = normalizeRotation(targetRotation.x, groupRef.current.rotation.x);
				targetRotation.y = normalizeRotation(targetRotation.y, groupRef.current.rotation.y);
				targetRotation.z = normalizeRotation(targetRotation.z, groupRef.current.rotation.z);
				// Flip the cube if it's upside down
				if (!isCubeUpright(targetRotation, new THREE.Vector3(0, 1, 0))) {
					targetRotation.z -= Math.PI / 2;
					targetRotation.y += Math.PI;
					targetRotation.x += Math.PI / 2;
				}
				break;
			case 4: // Front -- Marketing
				quaternion.current.set(0, 0, 0, 1);
				targetRotation = new THREE.Euler(0, 0, 0);
				break;
			case 5: // Left -- Sport
				quaternion.current.setFromAxisAngle(new THREE.Vector3(0, 1, 0), Math.PI / 2);
				targetRotation = new THREE.Euler(0, Math.PI / 2, 0);
				targetRotation.x = normalizeRotation(targetRotation.x, groupRef.current.rotation.x);
				targetRotation.y = normalizeRotation(targetRotation.y, groupRef.current.rotation.y);
				targetRotation.z = normalizeRotation(targetRotation.z, groupRef.current.rotation.z);
				// Flip the cube if it's upside down
				if (!isCubeUpright(targetRotation, new THREE.Vector3(0, 1, 0))) {
					targetRotation.z += Math.PI / 2;
					targetRotation.y += Math.PI;
					targetRotation.x += Math.PI / 2;
				}
				break;
			default:
				targetRotation = new THREE.Euler(0, 0, 0);
		}

		// Start the spring animation
		api.start({
			from: { springRotation: [groupRef.current.rotation.x, groupRef.current.rotation.y, groupRef.current.rotation.z] },
			to: { springRotation: [targetRotation.x, targetRotation.y, targetRotation.z] },
		});
	};

	// find the camera parent in the scene
	const cameraParentRef = scene.children.find((child) => child.name === "Camera Parent") as THREE.Group;
	if (!cameraParentRef) {
		throw new Error("Camera parent not found in the scene");
	} else {
		cameraParentRef.add(camera);
	}

	const [portalTransitionSpring, transitionApi] = useSpring(() => ({
		cameraPosition: [0, 0, 0],
		backgroundColor: "#ffffff", // Initial background color
		config: { mass: 1, tension: 300, friction: 80, precision: 0.01, clamp: true },
	}));

	const enterPortal = (portalId: number) => {
		setCurrentPortal(portals[portalId]);
		transitionApi.start({
			to: { cameraPosition: [0, 0.5, -2], backgroundColor: "#000000" },
			onStart: () => {
				animatingTransition.current = true;
			},
			onRest: () => {
				animatingTransition.current = false;
				setIsInPortal(true);
			},
		});
	};

	const exitPortal = () => {
		setIsInPortal(false);
		setCurrentPortal(null);
		transitionApi.start({
			to: { cameraPosition: [0, 0, 0], backgroundColor: "#ffffff" },
			onStart: () => {
				animatingTransition.current = true;
			},
			onRest: () => {
				animatingTransition.current = false;
				calculateVisiblePortals();
			},
		});
	};

	useFrame(() => {
		if (!isInPortal) {
			calculateVisiblePortals();
		}

		if (animatingTransition.current) {
			const [x, y, z] = portalTransitionSpring.cameraPosition.get();
			cameraParentRef.position.set(x, y, z);
			scene.background = new THREE.Color(portalTransitionSpring.backgroundColor.get());
		}
	});

	useEffect(() => {
		window.addEventListener("pointerdown", handlePointerDown);
		window.addEventListener("pointerup", handlePointerUp);
		window.addEventListener("pointermove", handlePointerMove);

		// Calculate initial visibility
		calculateVisiblePortals();

		return () => {
			window.removeEventListener("pointerdown", handlePointerDown);
			window.removeEventListener("pointerup", handlePointerUp);
			window.removeEventListener("pointermove", handlePointerMove);
		};
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, []);

	return (
		<>
			{!isInPortal && (
				<animated.group
					ref={groupRef} /// @ts-expect-error Rotation is not assignable to type 'THREE.Euler', but actually it is
					rotation={rotation.springRotation}
				>
					{visiblePortals.map((id) => {
						const { node, texture, header, tagline } = portals[id];
						return (
							<PortalObject
								key={id}
								node={node}
								texture={texture}
								header={header}
								tagline={tagline}
								inTransition={animatingTransition.current}
								onEnterPortal={() => enterPortal(id)}
							/>
						);
					})}
				</animated.group>
			)}

			{/* Render portal mesh when inside a portal */}
			{isInPortal && currentPortal && (
				<>
					<BackButton onClick={exitPortal}>Back</BackButton>
					<group position={groupRef.current?.position} rotation={groupRef.current?.rotation}>
						<group position={currentPortal.node.position} rotation={currentPortal.node.rotation}>
							<LoadPortalScene portalId={currentPortal.id} maxScroll={1000}>
								<PortalText header={currentPortal.header} tagline={currentPortal.tagline || ""} />
								<mesh geometry={currentPortal.node.geometry} position={[0, -0.5, -3]} scale={1.5}>
									<meshBasicMaterial map={currentPortal.texture} />
								</mesh>
							</LoadPortalScene>
						</group>
					</group>
				</>
			)}
		</>
	);
};

function PortalObject({
	node,
	texture,
	header,
	inTransition,
	onEnterPortal,
	tagline = "",
}: {
	node: THREE.Mesh;
	texture: THREE.Texture;
	header: string;
	inTransition: boolean;
	onEnterPortal: () => void;
	tagline?: string;
}) {
	const { geometry, position, rotation } = node;
	const [animateOpacity, setAnimateOpacity] = useState(false);
	const [showText, setShowText] = useState(false);
	const [hovered, setHovered] = useState(false);
	const [Opacity, setOpacity] = useState(0);
	useCursor(hovered);

	const { scale } = useSpring({
		scale: hovered ? 1.1 : 1,
		config: { mass: 2, tension: 400, friction: 40 },
	});

	const { textOpacity } = useSpring({
		textOpacity: showText ? 1 : 0,
		config: { duration: 500 },
		onStart: () => {
			setAnimateOpacity(true);
		},
		onRest: () => {
			setAnimateOpacity(false);
		},
	});

	useFrame(() => {
		if (animateOpacity) {
			setOpacity(textOpacity.get());
		}
	});

	const fontSize = header === "Sport &\nCommunication" ? 0.3 : 0.4;
	const textHeight = header === "Sport &\nCommunication" ? 1.2 : 1.4;

	return (
		<animated.mesh
			geometry={geometry}
			position={position}
			rotation={rotation}
			scale={scale}
			onPointerOver={() => {
				setHovered(true);
				setShowText(true);
			}}
			onPointerOut={() => {
				setHovered(false);
				if (!inTransition) {
					setShowText(false);
				}
			}}
			onClick={() => {
				setShowText(true);
				setHovered(false);
				onEnterPortal();
			}}
		>
			<MeshPortalMaterial>
				<Text
					font="/cube-test/fonts/RedHatDisplay-SemiBold.ttf"
					color="#ffffff"
					anchorY="top"
					anchorX="left"
					position={[-1.1, 2, 1]}
					fontSize={fontSize}
				>
					{header}
				</Text>
				{tagline && (
					<Text
						font="/cube-test/fonts/RedHatDisplay-Regular.ttf"
						color="#ffffff"
						fillOpacity={Opacity}
						anchorY="top"
						anchorX="left"
						position={[-1, textHeight, 1]}
						fontSize={0.1}
						maxWidth={2} // Set the maximum width for the text
						lineHeight={1.2} // Adjust the line height if needed
						overflowWrap="break-word" // Ensure words break correctly
					>
						{tagline}
					</Text>
				)}
				<mesh geometry={geometry} position={[0, -0.5, -3]} scale={1.5}>
					<meshBasicMaterial map={texture} />
				</mesh>
			</MeshPortalMaterial>
		</animated.mesh>
	);
}

export default NpgPortalCube;

const PortalText: React.FC<{ header?: string; tagline?: string }> = ({ header, tagline }) => {
	const fontSize = header === "Sport &\nCommunication" ? 0.3 : 0.4;
	const textHeight = header === "Sport &\nCommunication" ? 1.2 : 1.4;
	return (
		<>
			<Text font="/cube-test/fonts/RedHatDisplay-SemiBold.ttf" color="#ffffff" anchorY="top" anchorX="left" position={[-1.1, 2, 1]} fontSize={fontSize}>
				{header}
			</Text>
			<Text
				font="/cube-test/fonts/RedHatDisplay-Regular.ttf"
				color="#ffffff"
				anchorY="top"
				anchorX="left"
				position={[-1, textHeight, 1]}
				fontSize={0.1}
				maxWidth={2} // Set the maximum width for the text
				lineHeight={1.2} // Adjust the line height if needed
				overflowWrap="break-word" // Ensure words break correctly
			>
				{tagline}
			</Text>
		</>
	);
};
