import React, { useState } from "react";
import { IconButton, Menu, MenuItem, Box, Checkbox, Typography } from "@mui/material";
import MenuIcon from "@mui/icons-material/Menu";

interface HamburgerMenuProps {
  showStats?: boolean;
  onToggleStats?: () => void;
  usePerformanceMode?: boolean;
  onTogglePerformanceMode?: () => void;
  debugMode?: boolean;
  onToggleDebugMode?: () => void;
}

const HamburgerMenu: React.FC<HamburgerMenuProps> = ({
  showStats,
  onToggleStats,
  usePerformanceMode,
  onTogglePerformanceMode,
  debugMode,
  onToggleDebugMode,
}) => {
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);

  const handleClick = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <Box id="hamburger-menu" sx={{ position: "absolute", top: 0, right: 0, padding: 2, pointerEvents: "auto", zIndex: 1001 }}>
      <IconButton edge="start" color="default" aria-label="menu" onClick={handleClick}>
        <MenuIcon />
      </IconButton>
      <Menu anchorEl={anchorEl} open={Boolean(anchorEl)} onClose={handleClose}>
        <MenuItem onClick={onToggleStats}>
          <Checkbox color="default" checked={showStats} onChange={onToggleStats} />
          <Typography variant="body1">Show Stats</Typography>
        </MenuItem>
        <MenuItem onClick={onTogglePerformanceMode}>
          <Checkbox color="default" checked={usePerformanceMode} onChange={onTogglePerformanceMode} />
          <Typography variant="body1">Performance Mode</Typography>
        </MenuItem>
        <MenuItem onClick={onToggleDebugMode}>
          <Checkbox color="default" checked={debugMode} onChange={onToggleDebugMode} />
          <Typography variant="body1">Debug Mode</Typography>
        </MenuItem>
      </Menu>
    </Box>
  );
};

export default HamburgerMenu;
